html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: auto;
}
.example-card {
  background-color: lightblue;
  border-radius: 10px;
  padding: 10px;
  padding-top: 4px;
  margin: 10px;
  width: fit-content;

  display: flex;
  flex-direction: column;
}

.cm-editor {
  margin: 40px;
  height: 100%;
  margin-right: 60px;
}

em {
  text-decoration: underline;
  font-style: normal;
}
#header {
  position: fixed;
  top: 10px;
  right: 10px;
  padding: 5px;
  cursor: pointer;
  z-index: 2;
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(220, 220, 220);

  opacity: 0;
  visibility: hidden;
  display: flex;
  justify-content: center;

  z-index: 1;
  transition:
    visibility 0.3s linear,
    opacity 0.3s linear;
}
.menu-open {
  visibility: visible;
  opacity: 1;
}

#main-content {
  display: flex;
  align-items: center;
  justify-content: center;
}

#left-panel {
  flex: 1;
  padding: 20px;
}

#buttons {
  /*flex: 1;*/
  padding-left: 20px;
  padding-right: 20px;
  display: inline-flex;
  text-align: center;
  background-color: #fff;
  padding-top: 150px;
  height: 100%;
}

.button {
  display: block;
  margin: 10px;
  margin-top: 20px;
  padding: 10px 20px;
  width: 200px;
  text-align: center;
  /*background-color: #888;*/
  color: #333;
  font: 20px Roboto;
  text-decoration: none;
  border-radius: 5px;
  /*font-weight: bold;*/
  transition: background-color 0.3s;
}

.button:hover {
  /*background-color: #555;*/
  font-weight: bold;
}

.hint-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.hb {
  display: None;
  background-color: white;
  color: white;
  border: none;
  border-radius: 50%;
  font-size: 16px;
  cursor: pointer;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
}

#suggest_box {
  max-width: 30%;
  min-height: 26px;
  position: fixed;
  bottom: 80px;
  right: 20px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
  padding: 4px;
  overflow-wrap: break-word;
  text-align: justify;
  font-family: monospace;
  font-size: 18px;
  display: none;
}
